.wv-section-slider-products {

  // .woocommerce-loop-product_type_slider 
  .woocommerce-loop-product {

    // WV note
    &__preview-btn {
      display: none;
    }

    // .woocommerce-loop-product__title
    &__title {
      padding: 0 20px 10px 20px;
    }


    .variations_form {
      padding: 0 20px 0 20px;
    }

    .woocommerce-variation-add-to-cart {
      .wv-add-to-cart {
        background: $primary;

        &:hover {
          background: $body-color;
          // box-shadow: 0px 1px 5px #E2195B;
          border: 1px solid $body-color;

          span {
            color: #fff;
          }

          .wv-icon {
            use {
              stroke: #fff;
            }
          }
        }

        .wv-icon {
          use {
            stroke: #fff;
          }
        }

        span {
          color: #fff;
        }
      }
    }

    .wv-variations {
      display: none;
    }

    .variations_form {
      table {
        display: none;
      }

      .woocommerce-variation-add-to-cart {
        .quantity {
          display: none;
        }
      }
    }


    .wv-product-attributes {
      margin-bottom: 14px;
      padding: 0 20px;
      display: flex;

      &__item {
        margin-right: 4px;
      }
    }
  }
}